// 过载告警页面

<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>

      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
    </template>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>过载告警</span>
        <div style="float: right; padding: 3px 0">
          <el-button size="mini" type="primary" plain @click="setClick"
            >告警配置</el-button
          >
        </div>
        <!-- <div style="float: right; padding: 3px 6px">
          <el-button size="mini" type="danger" plain @click="deleteClick"
            >告警清除</el-button
          >
        </div> -->
      </div>

      <el-table
        v-loading="loading"
        :data="tableData"
        size="small"
        stripe
        highlight-current-row
        style="width: 100%; margin-top: 20px"
      >
        <el-table-column
          type="index"
          :index="indexMethod"
          width="50"
          label="序号"
        ></el-table-column>
        <!-- <el-table-column align="center" prop="id" label="ID"></el-table-column> -->
        <el-table-column
          align="center"
          prop="crt_time"
          label="时间"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cpu_load"
          label="当前CPU负载值（单位%）"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="mem_load"
          label="当前内存负载值（单位%）"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="hd_load"
          label="当前硬盘负载值（单位%）"
        ></el-table-column>
      </el-table>
    </el-card>
    <br />
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalRows"
      :current-page.sync="currentPage"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
    <el-dialog
      title="告警配置"
      :visible.sync="dialogVisible"
      width="600px"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="110px">
        <el-form-item label="告警开关">
          <el-switch
            v-model="form.alarm_switch"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
        <el-form-item label="CPU告警值">
          <el-input v-model.number="form.alarm_hd_threshold"></el-input>
        </el-form-item>
        <el-form-item label="内存告警值">
          <el-input v-model.number="form.alarm_cpu_threshold"></el-input>
        </el-form-item>
        <el-form-item label="硬盘告警值">
          <el-input v-model.number="form.alarm_mem_threshold"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitConfig">确 定</el-button>
      </div>
    </el-dialog>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetAlarmConfig,
  fetchGetInstanceList,
  fetchSetAlarmConfig,
  fetchGetAlarmOverLoad,
  fetchDelAlarmOvcerLoad,
} from "@/api/demo/index";
export default {
  name: "alarm",
  data() {
    return {
      totalRows: 0,
      currentPage: 1,
      tableData: [],
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      timer: null,
      isFirst: true,
      timerInterval: 10000,
      dialogVisible: false,
      form: {
        alarm_switch: false,
        alarm_mem_threshold: null,
        alarm_cpu_threshold: null,
        alarm_hd_threshold: null,
      },
      rules: {
        alarm_mem_threshold: [
          { required: false, message: "请输入", trigger: "blur" },
          { type: "number", message: "必须输入数字", trigger: "change" },
        ],
        alarm_hd_threshold: [
          { required: false, message: "请输入", trigger: "blur" },
          { type: "number", message: "必须输入数字", trigger: "blur" },
        ],
        alarm_hd_threshold: [
          { required: false, message: "请输入", trigger: "blur" },
          { type: "number", message: "必须输入数字", trigger: "blur" },
        ],
      },
      overload: {
        cpu_load: 0,
        mem_load: 0,
        hd_load: 0,
      },
      linkbroken: {},
      chartOption: [],
      loading: false,
    };
  },
  mounted() {
    this.getService();
    // this.drawLine();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      this.getOverload();
      // //清除定时并重启
      // if (this.timer) {
      //   clearInterval(this.timer);
      //   this.timer = null;
      //   // this.setTimer();
      // } else {
      //   // this.setTimer();
      // }
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.isFirst === true && this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
          this.isFirst = false;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    setTimer() {
      if (this.timer == null) {
        this.timer = setInterval(() => {
          console.log("开始定时...每过一-执行一次");
          this.getOverload();
        }, this.timerInterval);
      }
    },
    setClick() {
      this.getAlarmConfig();
      this.dialogVisible = true;
    },
    // 获取过载告警数据
    getOverload() {
      this.loading = true;
      console.log();
      fetchGetAlarmOverLoad(
        {
          request_type: "alarm get",
          instance_id: this.instanceValue,
          sequence_no: 33431,
        },
        this.currentPage
      )
        .then((response) => {
          this.loading = false;
          console.log("alarm get:", response);
          if (response == null) {
            return;
          }
          this.tableData = response.rows;
          this.totalRows = response.total;
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    indexMethod(index) {
      return (this.currentPage - 1) * 10 + index;
    },
    //分页按钮的触发事件
    handleCurrentChange() {
      this.getOverload();
    },
    getAlarmConfig() {
      fetchGetAlarmConfig({
        request_type: "alarm get",
        instance_id: this.instanceValue,
        sequence_no: 3342,
      })
        .then((response) => {
          this.form = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },

    handleClose() {
      this.dialogVisible = false;
    },
    submitConfig() {
      this.dialogVisible = false;
      fetchSetAlarmConfig({
        request_type: "alarm set",
        instance_id: this.instanceValue,
        sequence_no: 3001,
        data: this.form,
      })
        .then((response) => {})
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 告警清除
    deleteClick() {
      this.$confirm("是否清除数据？", "确认信息", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      })
        .then(() => {
          this.loading = true;
          fetchDelAlarmOvcerLoad({
            request_type: "alarm del",
            instance_id: this.instanceValue,
            sequence_no: 33432,
          })
            .then((response) => {
              this.loading = false;
              this.$message({
                type: "su",
                message: "操作成功",
              });
            })
            .catch((e) => {
              this.loading = false;
              console.log(e);
              this.$alert(e, "网络请求发生错误", {
                confirmButtonText: "确定",
                callback: (action) => {},
              });
            });
        })
        .catch((action) => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>
